<template>
    <div class="form-general">
        <v-dialog
            v-model="dialog"
            max-width="350"
            persistent
        >
        <v-card>
        <v-card-title class="headline">Gracias. Su informacion ha sido correctamente enviada</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="red darken-1"
            text
            to="/"
          >
            Salir de la pagina
          </v-btn>

        </v-card-actions>
        </v-card>
        </v-dialog>
        <v-form @submit.prevent="addEvaluation()" ref="EvaluationForm" v-model="valid" lazy-validation>
            <v-row>
               <v-col cols="12" md="4">
                   <h4 class="title">
                       Experiencia en docencia universitario
                   </h4>
                   <v-text-field
                        color="secondary"
                        v-model.number="evaluation.items.numAnios"
                        :rules="anioRules"
                        label="Escriba sus años"
                        required
                    ></v-text-field>
               </v-col>

               <v-col cols="12">
                   <h4 class="title">
                       Disponibilidad de horario, incluyendo fines de semana
                   </h4>
                   <v-radio-group v-model="evaluation.items.disponibilidad">
                    <v-radio
                        label="SI"
                        value="5"
                        color="secondary"
                    ></v-radio>
                    <v-radio
                        label="NO"
                        value="0"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>

               <v-col cols="12">
                   <h4 class="title">
                       Disposición en entrevista
                   </h4>
                   <v-radio-group v-model="evaluation.items.disposicion">
                    <v-radio
                        label="SI"
                        value="5"
                        color="secondary"
                    ></v-radio>
                    <v-radio
                        label="NO"
                        value="0"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>

               <v-col cols="12">
                     <h4 class="title">Indicaciones: Seleccione del 1 al 5, siendo el 1 el dominio mínimo y 5 el dominio máximo</h4>
                </v-col>

               <v-col cols="12">
                   <h4 class="title">
                       Amplios conocimientos sobre la asignatura a virtualizar
                   </h4>
                   <v-radio-group v-model="evaluation.items.conocimiento">
                    <v-radio
                        v-for="(item, index) in optConocimientos" :key="index"
                        :label="index+1"
                        :value="item"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>

               <v-col cols="12">
                   <h4 class="title">
                       Manejo intermedio de Microsoft PowerPoint
                   </h4>
                   <v-radio-group v-model="evaluation.items.powerpoint">
                    <v-radio
                        v-for="(item, index) in optPowerpoint" :key="index"
                        :label="index+1"
                        :value="item"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>

               <v-col cols="12">
                   <h4 class="title">
                       Conocimientos de educación a distancia
                   </h4>
                   <v-radio-group v-model="evaluation.items.eduDistancia">
                    <v-radio
                        v-for="(item, index) in opteduDistancia" :key="index"
                        :label="index+1"
                        :value="item"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>

               <v-col cols="12">
                   <h4 class="title">
                       Manejo de plataformas LMS (Learning Management System)
                   </h4>
                   <v-radio-group v-model="evaluation.items.LMS">
                    <v-radio
                        v-for="(item, index) in optLMS" :key="index"
                        :label="index+1"
                        :value="item"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>

               <v-col cols="12">
                   <h4 class="title">
                       Responsabilidad notoria
                   </h4>
                   <v-radio-group v-model="evaluation.items.responsabilidad">
                    <v-radio
                        v-for="(item, index) in optResponsabilidad" :key="index"
                        :label="index+1"
                        :value="item"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>

               <v-col cols="12">
                   <h4 class="title">
                       Excelente comunicación oral y escrita
                   </h4>
                   <v-radio-group v-model="evaluation.items.comunicacion">
                    <v-radio
                        v-for="(item, index) in optComunicacion" :key="index"
                        :label="index+1"
                        :value="item"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>

                <v-col cols="12">
                   <h4 class="title">
                      Interes por la investigacion tecnica
                   </h4>
                   <v-radio-group v-model="evaluation.items.investigacion" :rules="investigacionRules">
                    <v-radio
                        v-for="(item, index) in optInvestigacion" :key="index"
                        :label="index+1"
                        :value="item"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>

               <v-col cols="12">
                   <h4 class="title">
                      Capacidad para interactuar con profesionales y captar instrucciones
                   </h4>
                   <v-radio-group v-model="evaluation.items.interactuar" :rules="interactuarRules">
                    <v-radio
                        v-for="(item, index) in optInteractuar" :key="index"
                        :label="index+1"
                        :value="item"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>    
               <v-col>
                   <v-btn
                   :disabled="!valid"
                    type="submit"
                    color="error"
                    class="mr-4"
                    @click="validate()"
                    @click.stop="dialog = true"
                    >
                    Finalizar
                    </v-btn>
                    
                </v-col>          
            </v-row>
           
        </v-form>
    </div>
</template>

<script>
import router from '../router/index';
export default {
    name: 'Form-evaluation',
    data() {
        return {
            valid: true,
            dialog: false,
            optConocimientos:[3, 6, 9, 12, 15],
            optPowerpoint: [1, 2, 3, 4, 5],
            opteduDistancia: [4,8,12,16,20],
            optLMS: [3,6,9,12,15],
            optResponsabilidad: [1,2,3,4,5],
            optComunicacion: [1,2,3,4,5],
            optInvestigacion: [2,4,6,8,10],
            optInteractuar: [1,2,3,4,5],

            evaluation:{
                items:{
                    numAnios: null,
                    anios: null,
                    disponibilidad: null,
                    disposicion: null,
                    conocimiento: null,
                    powerpoint: null,
                    eduDistancia: null,
                    LMS: null,
                    responsabilidad: null,
                    comunicacion: null,
                    investigacion: null,
                    interactuar: null
                },
                total_score: null,
                IdGeneral: ''
            },
            anioRules:[
                v => !!v || 'Experiencia en docencia universitario es requerido'
            ],
            investigacionRules:[
                v => !!v || 'Interes por la investigacion tecnica es requerido'
            ],
            interactuarRules:[
                v => !!v || 'Capacidad para interactuar con profesionales y captar instrucciones'
            ]
        }
    },
    methods: {
        validate(){
            this.$refs.EvaluationForm.validate()
        },
        addEvaluation(){
            if(this.evaluation.items.numAnios >= 5){
                this.evaluation.items.anios = 20
            }
            this.evaluation.total_score = this.evaluation.items.anios + this.evaluation.items.conocimiento + this.evaluation.items.powerpoint + this.evaluation.items.eduDistancia + this.evaluation.items.LMS + this.evaluation.items.responsabilidad + this.evaluation.items.comunicacion + this.evaluation.items.investigacion + this.evaluation.items.interactuar
            this.evaluation.IdGeneral = this.$route.params.id
            
            this.axios.post('evaluations/new-evaluation', this.evaluation)
                .then((res) => {
                   console.log(res);
                }).catch((err) => {
                    console.log(err)
                });
        }
    }
}
</script>